										v-model="slider.enabled"
										:label="slider.label"
										:off-icon="slider.icon"
										:on-icon="slider.icon"
										class="text-capitalize"
										color="accent"
									></v-checkbox>
								</v-col>
								<!--	 Special slider choices		-->
								<v-col
									v-for="slider in recommendationsForm.optionalParams.specialSliders"
									:key="slider.param"
									:class="sliderCheckboxBreakpoints"
									cols="12" lg="2" md="3" sm="5">
									<v-checkbox
										v-model="slider.enabled"
										:label="slider.label"
										:off-icon="slider.icon"
										:on-icon="slider.icon"
										class="text-capitalize"
										color="accent"
									></v-checkbox>
								</v-col>
							</v-row>

							<!-- Parameter controls -->
							<v-divider class="mb-15"></v-divider>
							<v-row
								v-for="slider in recommendationsForm.optionalParams.sliderParams"
								:key="slider.param"
							>
								<v-col v-if="slider.enabled" class="mt-n4" cols="1">
									<v-radio-group v-model="slider.type" dense row>
										<v-radio
											color="accent"
											label="Equal"
											value="target"
										></v-radio>
										<v-radio
											color="accent"
											label="Min"
											value="min"
										></v-radio>
										<v-radio
											color="accent"
											label="Max"
											value="max"
										></v-radio>
									</v-radio-group>
								</v-col>
								<v-col v-if="slider.enabled" cols="9">
									<v-slider
										v-model="slider.value"
										:append-icon="slider.icon"
										:max="slider.max"
										:min="slider.min"
										:step="slider.step"
										class="ml-6 mr-n3"
										thumb-color="accent"
										thumb-label="always"
										ticks="always"
										track-color="primary"
										track-fill-color="green"
