										target="_blank"
									>
										{{ item.album.name }}
									</a>
								</template>
								<template v-slot:item.artists[0].name="{ item }">
									<a
										:href="item.artists[0].external_urls.spotify"
										class="text--primary text-decoration-none"
										target="_blank"
									>
										{{ item.artists[0].name }}
									</a>
								</template>
							</v-data-table>
						</v-container>

						<!-- Top Artists Module -->
						<v-container v-if="moduleContainer.selectedModule === 'topArtists'">
							<h2 class="ml-2 mb-3">
								Your Top Artists
							</h2>
							<v-data-table
								:headers="topArtistsData.headers"
								:items="topArtistsData.artists"
								:items-per-page="5"
								calculate-widths
								item-key="id"
								no-data-text="No data!?"
								no-results-text="No results :C"
							>
								<template v-slot:item.images[0].url="{ item }">
									<v-avatar class="ma-1" size="75">
										<v-img
											:src="item.images[0].url"
										></v-img>
									</v-avatar>
								</template>
								<template v-slot:item.name="{ item }">
									<a
										:href="item.external_urls.spotify"
										class="text--primary text-decoration-none"
										target="_blank"
									>
										{{ item.name }}
									</a>
								</template>
							</v-data-table>
						</v-container>
					</v-card>
				</v-col>
			</v-row>
		</v-container>
	</v-container>
</template>

<script>
// import axios from '../config/audiohaven.js'
import router from "@/router";
import axios from "axios";
import {mapState} from "vuex";

require('dotenv').config();

export default {
	name: "Spotify",
	title: 'Audiohaven | Spotify Dashboard',
	components: {
		VuetifyAudio: () => import('vuetify-audio'),
	},
	data(){
		return{
