			refCount: 0,
			isLoading: false,
			overlay: false,
			// Spotify Data //
			displaySpotifyStatusMessage: false,
			spotifyStatusMessage: "",
			snackbarColour: "purple darken-1",
			spotifyUserData: {},
			currentSpotifyPlaylist: null,
