					},
					{
						text: 'Followers',
						value: 'followers.total'
					},
				],
				followedArtists: [],
			},
			// Playlist Data Table //
			playlistLayer: 0,
			playlistTable: [
				{
					// Playlists Table Data (Layer One) //
					// < Each item is a playlist > //
					Search: "",
					Sort: 'tracks.total',
					SortDesc: true,
					Headers: [
						{
							text: 'ID',
							value: 'id',
							align: ' d-none' // ' d-none' hides the column but keeps the search ability
						},
						{
							text: 'Name',
							value: 'name',
							align: 'start',
							width: 400
						},
						{
							text: 'Tracks',
							value: 'tracks.total'
						},
						{
							text: 'Public',
							value: 'public'
						},
						{
							text: 'Collab',
							value: 'collaborative'
						},
						{
							text: 'Delete',
							value: 'delete',
							sortable: false
						},
					],
					Items: [],
					Selected: [],
					singleExpand: true,
					Expanded: [],
				},
				{
					// Playlists Table Data (Layer Two) //
					// < Each item is a track > //
					Search: "",
					Sort: 'added_at',
					SortDesc: true,
					Headers: [
						{
							text: 'ID',
							value: 'track.id',
							align: ' d-none'
						},
						{
							text: 'Title',
							value: 'track.name',
							align: 'start',
							width: 300
						},
						{
							text: 'Artist',
							value: 'track.artists[0].name',
						},
						{
							text: 'Added',
							value: 'added_at',
							align: 'right'
						},
						{
							text: 'Remove',
							value: 'remove',
							sortable: false
						},
					],
					Items: [],
					Selected: [],
					singleExpand: true,
					Expanded: [],
				}
			],
			// Recommendations Form Data
			recommendationsForm: {
				// Spotify docs: https://developer.spotify.com/documentation/web-api/reference/#/operations/get-recommendations
				// Max params: Hard ceiling on track attribute's value
				// Min params: Hard floor on track attribute's value
				// Target params: Returns tracks with attribute values nearest to target

				// REQUIRED PARAMS //
				requiredParams:
					{
						// Up to 5 seed values may be provided in any combination of seed_artists, seed_tracks and seed_genres
						// A comma separated list of Spotify IDs for seed artists
						seed_artists: "",
						// Which search is enabled [options: 'artistSearch' and 'followedArtists']
						artistSeedType: "artistSearch",
						// Initialising string to be used for artist searches
						artistSearch: "",
						// Initialising array for artists returned by search on 'Search' endpoint
						artistOptions: [],
						// A comma separated list of any genres in the set of available genre seeds
						seed_genres: "",
						// Initialising array for available genres, retrieved from 'Get Available Genre Seeds' endpoint
						genreOptions: [],
						// A comma separated list of Spotify IDs for a seed track
						seed_tracks: "",
						// Initialising string to be used for track searches
						trackSearch: "",
						// Initialising array for tracks returned by search on 'Search' endpoint
						trackOptions: [],
					},

				// OPTIONAL PARAMS //
				optionalParamsEnabled: false,
				optionalParams:
					{
						// Amount of tracks returned (Default 20, Max 100)
						limitOptions: [
							{
								value: "1"
							},
							{
								value: "5"
							},
							{
								value: "25"
							},
							{
								value: "50"
							},
							{
								value: "100"
							},
						],
						limitSelected: "5",
						// An ISO 3166-1 alpha-2 country code
						market: "",
						// SLIDER PARAMETERS //
						sliderParams: [
							// The following accept values between 0 -> 1 (eg. 0.35) //
							{
								// How acoustic the tracks are
								enabled: false,
								label: "Acoustic",
								param: "acousticness",
								type: "target",
								value: null,
								icon: "mdi-guitar-acoustic",
								min: 0,
								max: 1,
								step: 0.1,
							},
							{
								// How "danceable" the tracks are
								enabled: false,
								label: "Danceable",
								param: "danceability",
								type: "target",
								value: null,
								icon: "mdi-human-female-dance",
								min: 0,
								max: 1,
								step: 0.1
							},
							{
								// How energetic the tracks are
								enabled: false,
								label: "Energy",
								param: "energy",
								type: "target",
								value: null,
								icon: "mdi-flash",
								min: 0,
								max: 1,
								step: 0.1
							},
							{
								// How instrumental the tracks are
								enabled: false,
								label: "Instrumental",
								param: "instrumentalness",
								type: "target",
								value: null,
								icon: "mdi-instrument-triangle",
								min: 0,
								max: 1,
								step: 0.1
							},
							{
								// How live the tracks sound
								enabled: false,
								label: "Live",
								param: "liveness",
								type: "target",
								value: null,
								icon: "mdi-theater",
								min: 0,
								max: 1,
								step: 0.1
							},
							{
								// The loudness of the tracks
								enabled: false,
								label: "Loud",
								param: "loudness",
								type: "target",
								value: null,
								icon: "mdi-volume-high",
								min: 0,
								max: 1,
								step: 0.1
							},
							{
								// How much speech is in the tracks
								enabled: false,
								label: "Speech",
								param: "speechiness",
								type: "target",
								value: null,
								icon: "mdi-account-voice",
								min: 0,
								max: 1,
								step: 0.1
							},
							{
								// How "happy" the tracks are
								enabled: false,
								label: "Uplifting",
								param: "valence",
								type: "target",
								value: null,
								icon: "mdi-emoticon-happy",
								min: 0,
								max: 1,
								step: 0.1
							},
							// Other range parameters //
							{
								// The key tracks are in (accepts values between 0 -> 11)
								enabled: false,
								label: "Key",
								param: "key",
								type: "target",
								value: null,
								icon: "mdi-piano",
								min: 0,
								max: 11,
								step: 1
							},
							{
								// Popularity of the tracks (accepts 0 -> 100)
								enabled: false,
								label: "Popularity",
								param: "popularity",
								type: "target",
								value: null,
								icon: "mdi-star-face",
								min: 0,
								max: 100,
								step: 10
							},
							{
								// Tempo of the tracks (accepts any integer)
								enabled: false,
								label: "BPM",
								param: "tempo",
								type: "target",
								value: null,
								icon: "mdi-metronome",
								min: 0,
								max: 200,
								step: 5
							},
						],
						// Special sliders
						specialSliders: [
							{
								// Duration slider
								enabled: false,
								label: "Duration",
								param: "max_duration_ms",
								type: "target",
								value: null,
								icon: "mdi-timer-outline",
								min: 0,
								max: 60,
								step: 5
							}
						],
					},
			},
			// Recommendations Response Data
			recommendationData: {
				headers: [
					{
						text: 'Art',
						value: 'album.images[0].url',
						align: 'left',
						sortable: false
					},
					{
						text: 'Title',
						value: 'name',
						align: 'left'
					},
					{
						text: 'Album',
						value: 'album.name',
						align: 'left'
					},
					{
						text: 'Artist',
						value: 'artists[0].name',
						align: 'left'
					},
				],
				response: [],
				seeds: [],
				artistSeed: "",
				trackSeed: "",
				genreSeed: "",
				somethingIsPlaying: false,
			},
			newPlaylistURL: "",
			// Recently Played Data //
			recentlyPlayedData: {
				headers: [
					{
						text: 'Art',
						value: 'track.album.images[0].url',
						align: 'left',
						sortable: false
					},
					{
						text: 'Title',
						value: 'track.name',
						align: 'left'
					},
					{
						text: 'Album',
						value: 'track.album.name',
						align: 'left'
					},
					{
						text: 'Artist',
						value: 'track.artists[0].name',
						align: 'left'
					},
					{
						text: 'When',
						value: 'played_at',
						align: 'left'
					},
				],
				tracks: [],
				when: [],
			},
			// Saved Tracks Data //
			savedTracksData: {
				headers: [
					{
						text: 'Art',
						value: 'track.album.images[0].url',
						align: 'left',
						sortable: false
					},
					{
						text: 'Title',
						value: 'track.name',
						align: 'left'
					},
					{
						text: 'Album',
						value: 'track.album.name',
						align: 'left'
					},
					{
						text: 'Artist',
						value: 'track.artists[0].name',
						align: 'left'
					},
				],
				tracks: [],
			},
			// Top Tracks Data //
			topTracksData: {
				headers: [
					{
						text: 'Art',
						value: 'album.images[0].url',
						align: 'left',
						sortable: false
					},
					{
						text: 'Title',
						value: 'name',
						align: 'left'
					},
					{
						text: 'Album',
						value: 'album.name',
						align: 'left'
					},
					{
						text: 'Artist',
						value: 'artists[0].name',
						align: 'left'
					},
				],
				tracks: [],
			},
			// Top Artists Data //
			topArtistsData: {
				headers: [
					{
						text: 'Profile Picture',
						value: 'images[0].url',
						align: 'left',
						sortable: false
					},
					{
						text: 'Name',
						value: 'name',
						align: 'left'
					},
					{
						text: 'Popularity',
						value: 'popularity',
						align: 'left'
					},
					{
						text: 'Followers',
						value: 'followers.total',
						align: 'left'
					},
				],
				artists: [],
			},
			// TODO: Playback Data
		}
	},
	watch: {
		// Watcher is monitoring xModuleSelected functions from computed
		// If the user chooses a different module, it runs any function required
		// (Implemented to try to cut down on CPU & RAM usage)
		userPlaylistSelected : function(selected){
			if(selected){
				console.log("userPlaylistSelected")
				this.getUserPlaylists()
			} else {
				console.log("userPlaylistUnselected")
			}
		},
		savedTracksSelected : function(selected){
			if(selected){
				console.log("savedTracksSelected")
				this.getSavedTracks()
			} else {
				console.log("savedTracksUnselected")
			}
		},
		topTracksSelected : function(selected){
			if(selected){
				console.log("topTracksSelected")
				this.getTopTracks()
			} else {
				console.log("topTracksUnselected")
			}
		},
		recentlyPlayedSelected : function(selected){
			if(selected){
				console.log("recentlyPlayedSelected")
				this.getRecentlyPlayed()
			} else {
				console.log("recentlyPlayedUnselected")
			}
		},
		followedArtistsSelected : function(selected){
			if(selected){
				console.log("followedArtistsSelected")
				this.getFollowedArtists()
			} else {
				console.log("followedArtistsUnselected")
			}
		},
		topArtistsSelected : function(selected){
			if(selected){
				console.log("topArtistsSelected")
				this.getTopArtists()
			} else {
				console.log("topArtistsUnselected")
			}
		},
		recommendationGeneratorSelected : function(selected){
			if(selected){
				console.log("recommendationGeneratorSelected")
				this.getSpotifyGenres()
				this.getFollowedArtists()
			} else {
				console.log("recommendationGeneratorUnselected")
			}
		},
	},
	computed: {
		// Map loggedIn status & errors from Vuex store
		...mapState(['loggedIn', 'errors']),

		// If an item with key 'spotify_access_token' exists in localStorage,
		// return true. Otherwise, return false.
		spotifyLoggedIn(){
			return !!localStorage.getItem("spotify_access_token");
		},

		// If user ID was not found, return undefined.
		// Otherwise, return user ID.
		// (Unnecessary tbh.. leftover from userID bug testing)
		userID() {
			if(!this.spotifyUserData.id){
				return undefined
			} else {
				return this.spotifyUserData.id
			}
		},

		// If user has no profile picture, or user failed to load,
		// return kitten photo. Otherwise, return profile picture.
		userProfilePic() {
			// If user data failed to load, or if there is no image, return placeholder kitten
			if(this.spotifyUserData.images === undefined || this.spotifyUserData.images.length === 0){
				return "https://placekitten.com/80/80"
				// Else return user's avatar
			} else {
				return this.spotifyUserData.images[0].url
			}
		},

		// Check currently selected modules & return a boolean
		// Each returns true if module is selected, false if not
		userPlaylistSelected() {
			return this.selectedModulesHas('userPlaylists')
		},
		savedTracksSelected() {
			return this.selectedModulesHas('savedTracks')
		},
		topTracksSelected() {
			return this.selectedModulesHas('topTracks')
		},
		recentlyPlayedSelected() {
			return this.selectedModulesHas('recentlyPlayed')
		},
		followedArtistsSelected() {
			return this.selectedModulesHas('followedArtists')
		},
		topArtistsSelected() {
			return this.selectedModulesHas('topArtists')
		},
		recommendationGeneratorSelected() {
			return this.selectedModulesHas('recommendationGenerator')
		},

		// Filter out modules from module select which are already selected
		availableModules () {
			return this.modules.filter(key =>
				key.value !== this.moduleContainers[0].selectedModule
				&&
				key.value !== this.moduleContainers[1].selectedModule
				&&
				key.value !== this.moduleContainers[2].selectedModule
				&&
				key.value !== this.moduleContainers[3].selectedModule
			)
		},
		// Opposite of above
		selectedModules () {
			return this.modules.filter(key =>
				key.value === this.moduleContainers[0].selectedModule
				||
				key.value === this.moduleContainers[1].selectedModule
				||
				key.value === this.moduleContainers[2].selectedModule
				||
				key.value === this.moduleContainers[3].selectedModule
			)
		},

		// Data Iterator Computed Methods //
		numberOfFollowedArtistPages () {
			return Math.ceil(this.followedArtistsData.followedArtists.length / this.followedArtistsData.followedArtistsPerPage)
		},

		// DISABLED: Awkward to get working with followers.total
		// Made more sense to just 'hardcode' with only two other datasets (popularity/followers)
		// followedArtistFilteredKeys () {
		// 	return this.followedArtistsData.followedArtistKeys.filter(key => key.text !== 'Name')
		// },

		// making CSS color variables out of vuetify.js custom themes
		cssProps() {
			// create themeColors object
			let themeColors = {}
			let darkMode = localStorage.getItem("audiohaven_darkMode")
			// if dark mode is off,
			if (darkMode === false) {
				// for each color key in themes.light, add to themeColors, adding '--v-' to the start of the key
				// then assign this key the value of the color
				Object.keys(this.$vuetify.theme.themes.light).forEach((color) => {
					themeColors[`--v-${color}`] = this.$vuetify.theme.themes.light[color]
				})
				// else if dark mode is on
			} else {
				// fill themeColors with the colors from themes.dark instead
				Object.keys(this.$vuetify.theme.themes.dark).forEach((color) => {
					themeColors[`--v-${color}`] = this.$vuetify.theme.themes.dark[color]
				})
			}
			// return themeColors object
			return themeColors
		},

		// Recommender Methods //
		// Make sure at least one required seed is being used in recommender
		recommendationsRequired(){
			// if all required fields are empty, return false
			// else if even one is filled, return true
			if
			(
				!this.recommendationsForm.requiredParams.seed_artists
				&&
				!this.recommendationsForm.requiredParams.seed_genres
				&&
				!this.recommendationsForm.requiredParams.seed_tracks
			)
			{ return false }
			else { return true }
		},
		// Style sliders depending on viewport size
		sliderCheckboxBreakpoints(){
			// Switch case using current global breakpoint
			// If breakpoint is xs or sm, we want the content of the cols to be centered
			// Else if md/lg/xl, we want the contents to be left aligned
			switch (this.$vuetify.breakpoint.name) {
				case 'xs': return 'd-flex justify-center'
				case 'sm': return 'mr-1 d-flex justify-center'
				case 'md': return 'mr-1 d-flex justify-center'
				case 'lg': return 'mr-2 d-flex justify-center'
				case 'xl': return 'mr-2 d-flex justify-center'
			}
			// If none of the above, return nothing (required for a computed method)
			return ''
		},
		// Style slider radios depending on viewport size
		recommenderRowBreakpoints(){
			// If breakpoint is xs, put radio-group in column mode
			// Else, put radio-group in row mode
			switch (this.$vuetify.breakpoint.name) {
				case 'xs': return false
				case 'sm': return true
				case 'md': return true
				case 'lg': return true
				case 'xl': return true
			}
			return false
		}
	},

	mounted(){
		this.checkAudiohavenLogin()
		this.checkTokens()
		this.getFollowedArtists()
		this.getSpotifyGenres()
		this.getUserData()
		this.getRecentlyPlayed()
		this.getSavedTracks()
		this.getTopTracks()
		this.getTopArtists()
	},

	created() {
		// When an axios request is made, intercept it and:
		axios.interceptors.request.use((config) => {
			// assign setLoading to true
			this.setLoading(true);
			// return request config
			return config;
			// Otherwise:
		}, (error) => {
			// If user gets a 401, their access token likely expired- so refresh.
			if (error.response.status === 401) {
				this.refreshSpotifyToken()
			}
			// assign setLoading to false
			this.setLoading(false);
			// return a Promise Object with given reason
			return Promise.reject(error);
		});

		// When an axios response is received, intercept it and setLoading to false
		axios.interceptors.response.use((response) => {
			this.setLoading(false);
			return response;
		}, (error) => {
			if (error.response.status === 401) {
				this.refreshSpotifyToken()
			}
			this.setLoading(false);
			return Promise.reject(error);
		});
	},

	methods: {
		// Check if logged-in to Audiohaven
		checkAudiohavenLogin(){
			if(!this.loggedIn){
				// If not logged in, tell the user off & send them back to root!
				console.log("No no no.. Nice try though! 😉")
				router.push('/')
			}
		},

		// Return label of passed module (UNUSED)
		// Not using because it proved to be super heavy in module for-loop
		moduleLabel(module) {
			console.log(this.selectedModules)
			let filter = this.selectedModules.filter(key => key.value === module)
			console.log(filter)
			return filter[0].label
		},

		// Firstly, store selected moduleContainers in localStorage
		// Secondly, check if the selected modules contain passed moduleType
		selectedModulesHas(moduleType){
			if(this.spotifyLoggedIn) {
				// Part One //
				// for each container in moduleContainers
				this.moduleContainers.forEach((container, index) => {
					// (debug) log if the container includes passed moduleType
					// console.log('moduleContainer', index, `includes ${moduleType}?..`, Object.values(container).includes(moduleType))
					// contains = true if container includes passed moduleType, false if not
					let contains = Object.values(container).includes(moduleType)
					// if true
					if (contains) {
						// set in localStorage
						localStorage.setItem(`selectedModule_${index}`, moduleType)
					}
				})
				// Part Two //
				// const defining what some checks against
				// in this case, whether the value parameter === passed moduleType
				const hasType = (item) => item.value === `${moduleType}`
				// logging during testing
				// console.log(`some ${moduleType}?..`, this.selectedModules.some(hasType))
				// returns true if one of the selected modules matches moduleType
				// returns false if not
				return this.selectedModules.some(hasType)
			}
		},

		// If user chose their own modules,
		// load this choice / these choices instead of defaults.
		// Else, load defaults.
		firstModuleLocalStorage(){
			if(localStorage.getItem('selectedModule_0')){
				let module = localStorage.getItem('selectedModule_0')
				console.log("Module found in localStorage:", module)
				return module
			} else {
				console.log("No module found in localStorage!")
				return 'userPlaylists'
			}
		},
		secondModuleLocalStorage(){
			if(localStorage.getItem('selectedModule_1')){
				let module = localStorage.getItem('selectedModule_1')
				console.log("Module found in localStorage:", module)
				return module
			} else {
				console.log("No module found in localStorage!")
				return 'followedArtists'
			}
		},
		thirdModuleLocalStorage(){
			if(localStorage.getItem('selectedModule_2')){
				let module = localStorage.getItem('selectedModule_2')
				console.log("Module found in localStorage:", module)
				return module
			} else {
				console.log("No module found in localStorage!")
				return 'recentlyPlayed'
			}
		},
		fourthModuleLocalStorage(){
			if(localStorage.getItem('selectedModule_3')){
				let module = localStorage.getItem('selectedModule_3')
				console.log("Module found in localStorage:", module)
				return module
			} else {
				console.log("No module found in localStorage!")
				return 'recommendationGenerator'
			}
		},

		// Loading status method
		setLoading(isLoading) {
			// if loading
			if (isLoading) {
				// increment refCount
				this.refCount++;
				// assign isLoading to true
				this.isLoading = true;
				// else if refCount is higher than 1
			} else if (this.refCount > 0) {
				// decrement refCount
				this.refCount--;
				// assign isLoading to true/false depending on if more than 0 or not
				this.isLoading = (this.refCount > 0);
			}
		},

		// Set snackbar method
		setSnackbar(message, colour) {
			this.spotifyStatusMessage = message
			this.snackbarColour = colour
			this.displaySpotifyStatusMessage = true
		},

		// Logo Methods //
		spotifyLogo(type) {
			if (type === "text") {
				return require("@/assets/Spotify_logo_with_text.svg")
			} else {
				return require("@/assets/Spotify_logo_without_text.svg")
			}
		},

		// Data Iterator Methods //
		nextFollowedArtistPage () {
			if (this.followedArtistsData.followedArtistPage + 1 <= this.numberOfFollowedArtistPages) this.followedArtistsData.followedArtistPage += 1
		},
		formerFollowedArtistPage () {
			if (this.followedArtistsData.followedArtistPage - 1 >= 1) this.followedArtistsData.followedArtistPage -= 1
		},
		updateFollowedArtistsPerPage (number) {
			this.followedArtistsData.followedArtistsPerPage = number
		},

		// Data Table Methods //
		colorizeTableTracks(tracks) {
			if (tracks > 250) return 'purple'
			else if (tracks > 100) return 'purple darken-2'
			else if (tracks <= 100 && tracks > 0) return 'purple darken-4'
			else return '#333'
		},
		colorizeTableBooleans(boolean) {
			if (boolean) return 'purple darken-4'
			else return '#333'
		},
		dateParser(date){
			const parsedDate = new Date(date)
			return(`${parsedDate.getDate()}-${parsedDate.getMonth() +1}-${parsedDate.getFullYear()}`)
		},

		// Spotify Token Management //
		// Check if tokens exist as query parameters & store them if so.
		checkTokens(){
			// If there is both an access & refresh token in the URL
			if(this.$route.query.access_token && this.$route.query.refresh_token) {
				// Assign tokens as variables
				let access_token = this.$route.query.access_token
				let refresh_token = this.$route.query.refresh_token
				// Log tokens
				console.log("Tokens received through redirect URL!")
				// console.log("Access Token", access_token)
				// console.log("Refresh Token", refresh_token)
				// Store tokens in localStorage
				localStorage.setItem("spotify_access_token", access_token)
				localStorage.setItem("spotify_refresh_token", refresh_token)
				// Clear the URL bar (to prevent crazy errors when expiration occurs)
				router.replace('/Spotify')
				// Refresh page to update
				router.go(0)
			}
		},
		// Logout of Spotify, removing all relevant data from localStorage
		logoutSpotify(){
			// Remove tokens
			localStorage.removeItem("spotify_access_token")
			localStorage.removeItem("spotify_refresh_token")
			localStorage.setItem("spotify_user_id", "")
			// Replace URL with "/spotify" (to remove tokens if present)
			// Using catch & end to stop error from being thrown
			// (The error is due to the fact that technically we're routing back to the same place)
			router.replace("/Spotify").catch(()=>{})
			router.go(0)
		},
		// Once access token expires, use refresh token to get a new one
		refreshSpotifyToken(){
			let baseUrl = this.serverBaseURL
			let refresh_token = localStorage.getItem('spotify_refresh_token')
			axios
				.get(`${baseUrl}/spotify/refresh_token?refresh_token=${refresh_token}`, {
					headers: {
						"Content-Type": 'application/x-www-form-urlencoded'
					}
				})
				.then(response => {
						// Log response
						console.log("refreshSpotifyToken() response: ", response.data)
						// Assign local storage access token to new access token
						localStorage.setItem("spotify_access_token", response.data.access_token)
						// Re-run anything that would have failed with an expired token
						this.getFollowedArtists()
						this.getSpotifyGenres()
						this.getUserData()
						this.getRecentlyPlayed()
						this.getSavedTracks()
						this.getTopTracks()
						this.getTopArtists()
					}
				)
				.catch(error => {
					console.log("refreshSpotifyToken() error caught: ", error)
					console.log("refreshSpotifyToken() error message: ", error.message)
					this.setSnackbar(error.message, "red", )
				})
		},

		// Spotify API Requests //
		// Function for getting profile data from the user
		getUserData(){
			if(this.spotifyLoggedIn) {
				let token = localStorage.getItem('spotify_access_token')
				let spotifyBaseURL = 'https://api.spotify.com/v1'
				axios
					.get(`${spotifyBaseURL}/me/`,
						{
							headers: {
								"Authorization": `Bearer ${token}`
							}
						})
					.then(response => {
							console.log("getUserData() response: ", response.data)
							// Assign spotifyUserData to the response data
							this.spotifyUserData = response.data
							// Run getUserPlaylists
							// (Since it doesn't have the userID yet if run at mounted)
							this.getUserPlaylists()
							this.setSnackbar(`Welcome, ${response.data.display_name}! 👋`, "green")
						}
					)
					.catch(error => {
						console.log("getUserData() error caught: ", error)
						console.log("getUserData() error message: ", error.message)
						this.setSnackbar(error.message, "red")
					})
			}
		},
		// Function for getting user's followed artists
		getFollowedArtists(){
			// If user is logged-in
			if (this.spotifyLoggedIn && this.followedArtistsSelected || this.recommendationGeneratorSelected) {
				let token = localStorage.getItem('spotify_access_token')
				let spotifyBaseURL = 'https://api.spotify.com/v1'
				axios
					// GET request for followed artists, with the maximum limit of 50 set
					.get(`${spotifyBaseURL}/me/following?type=artist&limit=50`,
						{
							headers: {
								"Authorization": `Bearer ${token}`
							}
						})
					.then(response => {
							// Log response
							console.log("getFollowedArtists() response: ", response.data.artists.items)
							// Assign followedArtists to response.artists.items (followed artist array)
							this.followedArtistsData.followedArtists = response.data.artists.items
						}
					)
					.catch(error => {
						// Log errors
						console.log("getFollowedArtists() error caught: ", error)
						console.log("getFollowedArtists() error message: ", error.message)
						// Assign spotifyStatusMessage string to the value of error.message
						this.setSnackbar(error.message, "red")
					})
			}
		},
		// Function for getting user's saved playlists
		getUserPlaylists(selectedPlaylist) {
			if (this.spotifyLoggedIn && this.userPlaylistSelected) {
				let token = localStorage.getItem('spotify_access_token')
				let spotifyBaseURL = 'https://api.spotify.com/v1'
				if (this.userID) {
					// If no playlist was selected (ie we want list of playlists)
					if (!selectedPlaylist) {
						this.currentSpotifyPlaylist = null
						axios
							// GET request using user's ID
							.get(`${spotifyBaseURL}/users/${this.userID}/playlists?limit=50`,
								{
									headers: {
										"Authorization": `Bearer ${token}`
									}
								})
							.then(response => {
									// Log response
									console.log("getUserPlaylists() response: ", response.data)
									// Assign playlistTableItems to response.data.items (playlist array)
									this.playlistTable[0].Items = response.data.items
								}
							)
							.catch(error => {
								// Log error
								console.log("getUserPlaylists() error caught: ", error)
								console.log("getUserPlaylists() error message: ", error.message)
								// Assign spotifyStatusMessage string to the value of error.message
								this.setSnackbar(error.message, "red")
							})
					}
					// If a playlist was selected (ie we want list of tracks inside playlist)
					if (selectedPlaylist) {
						// console.log(`getUserPlaylists(${selectedPlaylist.id}): '${selectedPlaylist.name}' executed.`)
						this.currentSpotifyPlaylist = selectedPlaylist
						axios
							.get(`${spotifyBaseURL}/playlists/${selectedPlaylist.id}/tracks`,
								{
									headers: {
										"Authorization": `Bearer ${token}`
									}
								})
							.then(response => {
									console.log("getUserPlaylistTracks() response: ", response.data)
									// If we get a response, assign the second layer of playlistTable to it
									this.playlistTable[1].Items = response.data.items
									// Increment playlistLayer so the vuetify table changes too
									this.playlistLayer++
								}
							)
							.catch(error => {
								console.log("error caught: ", error)
								console.log("error message: ", error.message)
								this.setSnackbar(error.message, "red")
							})
					}
				}
			}
		},
		// Reset playlist layer to base layer
		decrementPlaylistLayer() {
			this.playlistLayer = 0
			this.currentSpotifyPlaylist = null
		},
		// Add track to Spotify queue
		queueSpotifyTrack(selected) {
			console.log(`queueSpotifyTrack(${selected.track.name})`)
			let token = localStorage.getItem('spotify_access_token')
			let spotifyBaseURL = 'https://api.spotify.com/v1'
			axios
				.post(`${spotifyBaseURL}/me/player/queue?uri=spotify:track:${selected.track.id}`,
					{},
					{
						headers: {
							"Authorization" : `Bearer ${token}`
						}
					})
				.then(response => {
						console.log("queueSpotifyTrack() SUCCESS! \n Response: ", response.data)
						this.setSnackbar(`Successfully added ${selected.track.name} to queue`, "green")
					}
				)
				.catch(error => {
					console.log("playSpotifyTrack() error caught: ", error.response, "\n Message: ", error.response.data.error.message)
					this.setSnackbar(`${error.response.data.error.message}...`, "red")
				})
		},
		// Play the Spotify preview (NON-FUNCTIONAL)
		previewSpotifyTrack(signal, track){
			// console.log(track.preview_url)
			// let trackPreview = new Audio(track.preview_url)
			if(signal==='play' && this.recommendationData.somethingIsPlaying){
				console.log(`Got ${signal} signal, but something is already playing!`)
				console.log(`Stopping any playing tracks!`)
				this.recommendationData.response.forEach(track => {
					if(track.isPlaying){
						console.log(`Stopping '${track.name}'!`)
						track.isPlaying = false
					}
				})
				console.log(`Now playing '${track.name}'`)
				// let trackPreviews = document.getElementsByTagName('audio')
				// let i = 0
				// for(i=0; i<trackPreviews.length; i++) trackPreviews[i].pause();
				// trackPreview.play()
				track.isPlaying = true
				this.recommendationData.somethingIsPlaying = true
			}
			if(signal==='play' && !this.recommendationData.somethingIsPlaying){
				console.log(`Got ${signal} signal, playing '${track.name}'`)
				// trackPreview.play()
				track.isPlaying = true
				this.recommendationData.somethingIsPlaying = true
			}
			if(signal==='pause'){
				console.log(`Got ${signal} signal, pausing '${track.name}'`)
				// trackPreview.pause()
				track.isPlaying = false
				this.recommendationData.somethingIsPlaying = false
			}
		},
		// Unfollow the Spotify playlist (remove)
		unfollowSpotifyPlaylist(playlist){
			let indexOfPlaylist = this.playlistTable[0].Items.indexOf(playlist)
			if (confirm(`Are you sure you want to unfollow ${playlist.name}?`)) {
				// Delete it!
				console.log(`Playlist '${playlist.name}' unfollow confirmed.`);
				// Unfollow Playlist Request
				let token = localStorage.getItem('spotify_access_token')
				let spotifyBaseURL = 'https://api.spotify.com/v1'
				axios
					.delete(`${spotifyBaseURL}/playlists/${playlist.id}/followers`,
						{
							headers: {
								"Authorization" : `Bearer ${token}`
							}
						})
					.then(response => {
							console.log("unfollowSpotifyPlaylist() SUCCESS! \n Response: ", response.data)
							this.setSnackbar(`Successfully unfollowed ${playlist.name}`, "green")
							this.playlistTable[0].Items.splice(indexOfPlaylist, 1)
						}
					)
					.catch(error => {
						console.log("unfollowSpotifyPlaylist() error caught: ", error.response, "\n Message: ", error.response.data.error.message)
						this.setSnackbar(`${error.response.data.error.message}...`, "red")
					})
			} else {
				// Do nothing!
				console.log(`Playlist '${playlist.name}' unfollow cancelled.`);
			}
		},
		// Remove track from Spotify playlist
		// TODO: Multiple track removal
		removeSpotifyPlaylistTrack(track, playlist){
			console.log("playlist: ", playlist, "\n", "track: ", track)
			let indexOfTrack = this.playlistTable[1].Items.indexOf(track)
			if (confirm(`Are you sure you want to remove ${track.track.name} from ${playlist.name}?`)) {
				// Delete it!
				console.log(`Track '${track.track.name}' removal confirmed.`);
				// Remove Track Request
				let token = localStorage.getItem('spotify_access_token')
				let spotifyBaseURL = 'https://api.spotify.com/v1'
				axios
					.delete(`${spotifyBaseURL}/playlists/${playlist.id}/tracks`,
						{
							headers: {
								"Authorization" : `Bearer ${token}`
							},
							data: {
								"tracks": [
									{"uri":`${track.track.uri}`},
								]
							}
						})
					.then(response => {
							console.log("removeSpotifyPlaylistTrack() SUCCESS! \n Response: ", response.data)
							this.setSnackbar(`Successfully removed ${track.track.name}`, "green")
							this.playlistTable[1].Items.splice(indexOfTrack, 1)
						}
					)
					.catch(error => {
						console.log("removeSpotifyPlaylistTrack() error caught: ", error.response, "\n Message: ", error.response.data.error.message)
						this.setSnackbar(`${error.response.data.error.message}...`, "red")
					})
			} else {
				// Do nothing!
				console.log(`Track '${track.name}' removal cancelled.`);
			}
		},
		// Get available genre seeds
		getSpotifyGenres(){
			// If user is logged-in
			if (this.spotifyLoggedIn && this.recommendationGeneratorSelected) {
				let token = localStorage.getItem('spotify_access_token')
